import React from "react";
import ReactDom from "react-dom";
import mockData from "./mockData.js";
import BrikwarsApp from "./BrikwarsApp.jsx";

export default () => {
  document.querySelectorAll(".js-brikwars-app").forEach((mount) => {
    if (!localStorage.getItem("brikwarsApp")) {
      localStorage.setItem("brikwarsApp", JSON.stringify(mockData));
    }

    ReactDom.render(
      <BrikwarsApp {...JSON.parse(localStorage.getItem("brikwarsApp"))} />,
      mount,
    );
  });
};
