import React, { useState, useEffect } from "react";
import { array } from "prop-types";
import cx from "classnames";

const propTypes = { modules: array, players: array };

const BrikwarsApp = (props) => {
  const { modules } = props;

  const [players, setPlayers] = useState([...props.players]);
  const [selectedPlayer, setSelectedPlayer] = useState(null);
  const [selectedModule, setSelectedModule] = useState(null);

  useEffect(() => {
    localStorage.setItem("brikwarsApp", JSON.stringify({ players, modules }));
  }, [modules, players]);

  const handleMovePlayer = (modObj, direction) => {
    let { row, col } = modObj;

    if (!selectedPlayer) {
      return;
    }

    switch (direction) {
      case "up":
        row === 0 ? row : row--;
        break;
      case "right":
        col === 9 ? col : col++;
        break;
      case "down":
        row === 9 ? row : row++;
        break;
      case "left":
        col === 0 ? col : col--;
        break;
    }

    setPlayers((players) => {
      return players.map((player) => {
        if (player.id === selectedPlayer.id) {
          return {
            ...player,
            modId: `id-${row + 1}-${col + 1}`,
            modRow: row,
            modCol: col,
            moved: direction,
          };
        }
        return player;
      });
    });

    setSelectedModule(null);
    setSelectedPlayer(null);
  };

  const handlePlayerClick = (player) => {
    if (selectedPlayer?.id === player.id) {
      setSelectedModule(null);
      setSelectedPlayer(null);
    } else {
      setSelectedPlayer(player);
      setSelectedModule(player.modId);
    }
  };

  const renderModule = (modObj) => {
    const selected = selectedModule === modObj.id;
    const occupants = players.filter((player) => player.modId === modObj.id);

    return (
      <div
        className={cx("brikwars-app__module", {
          "brikwars-app__module--selected": selected,
          [`brikwars-app__module--${modObj.theme}`]: modObj.theme,
          [`brikwars-app__module--${modObj.type}`]: modObj.type,
          [`brikwars-app__module--direction-${modObj.rotate}`]: modObj.rotate,
        })}
        key={modObj.id}
      >
        <div className="brikwars-app__mod-label">
          {modObj.label || modObj.id}
        </div>
        <div className="brikwars-app__players">
          {occupants.map((player) => (
            <button
              type="button"
              className={cx("brikwars-app__player", {
                "brikwars-app__player--selected":
                  player.id === selectedPlayer?.id,
              })}
              style={{
                "--background-color": player.background,
                "--text-color": player.color,
              }}
              key={player.id}
              onClick={() => handlePlayerClick(player)}
            >
              {player.name}
            </button>
          ))}
        </div>
        <button
          type="button"
          className="brikwars-app__move-button brikwars-app__move-button--up"
          onClick={() => handleMovePlayer(modObj, "up")}
          disabled={!selected}
        ></button>
        <button
          type="button"
          className="brikwars-app__move-button brikwars-app__move-button--right"
          onClick={() => handleMovePlayer(modObj, "right")}
          disabled={!selected}
        ></button>
        <button
          type="button"
          className="brikwars-app__move-button brikwars-app__move-button--down"
          onClick={() => handleMovePlayer(modObj, "down")}
          disabled={!selected}
        ></button>
        <button
          type="button"
          className="brikwars-app__move-button brikwars-app__move-button--left"
          onClick={() => handleMovePlayer(modObj, "left")}
          disabled={!selected}
        ></button>
      </div>
    );
    // case
    // }
  };

  return (
    <section className="brikwars-app">
      <div className="brikwars-app__interface">Players go here</div>
      <div className="brikwars-app__map">
        {modules.map((row, rowIndex) => {
          return row.map(renderModule);
        })}
      </div>
    </section>
  );
};
BrikwarsApp.propTypes = propTypes;
export default BrikwarsApp;
